import { Component, Input } from '@angular/core';
import { NgbActiveModal, NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { ViewUploadedFileComponent } from '../view-uploaded-file/view-uploaded-file.component';

@Component({
  selector: 'app-view-cash-deposits-modal',
  templateUrl: './view-cash-deposits-modal.component.html',
  styleUrls: ['./view-cash-deposits-modal.component.scss']
})
export class ViewCashDepositsModalComponent {
  @Input() depositDetails;
  fileName;
  constructor(public _modalService: NgbModal, public _activeModal: NgbActiveModal) {

  }
  ngOnInit() {
    this.fileName = this.depositDetails.filePath.split('/').pop();
  }
  viewFile(from) {
    const modalRef = this._modalService.open(ViewUploadedFileComponent, { size: 'md' });
    modalRef.componentInstance.file = (from == 'voucher') ? this.depositDetails?.depositVoucher : this.depositDetails?.filePath;
  }
}
